// src/components/MathJaxView.tsx
import React from 'react';
import { View, Text, Platform, StyleSheet, Dimensions } from 'react-native';
import { WebView } from 'react-native-webview';
import { renderToString } from 'katex';
import 'katex/dist/katex.min.css';

type Props = {
  tex: string;
};

const htmlTemplate = (math: string, display: boolean) => `
<!DOCTYPE html>
<html><head>
  <meta name="viewport" content="width=device-width, initial-scale=1.0">
  <link rel="stylesheet" href="https://cdn.jsdelivr.net/npm/katex@0.16.8/dist/katex.min.css">
  <style>
    body { margin:0; padding:0; }
    #math {
      display: ${display ? 'block' : 'inline-block'};
      white-space: normal !important;
      word-wrap: break-word;
      word-break: break-all;
      max-width: 100%;
      font-size: 1.2em;
      margin: ${display ? '0.5em 0' : '0 0.2em'};
    }
  </style>
</head><body>
  <div id="math"></div>
  <script src="https://cdn.jsdelivr.net/npm/katex@0.16.8/dist/katex.min.js"></script>
  <script>
    katex.render(
      String.raw\`${math}\`,
      document.getElementById('math'),
      { throwOnError: false, displayMode: ${display} }
    );
  </script>
</body></html>
`;

export default function MathJaxView({ tex }: Props) {
  // 1) On splitte en paragraphes pour gérer les sauts de ligne
  const paragraphs = tex.split(/\r?\n/).filter(p => p.length > 0);
  const { width } = Dimensions.get('window');

  return (
    <View style={styles.container}>
      {paragraphs.map((para, pIdx) => {
        // 2) Pour chaque paragraphe, on découpe math/non-math
        const parts = para.split(/(\$\$[\s\S]+?\$\$|\$[^$]+\$)/g).filter(Boolean);
        return (
          <View key={pIdx} style={styles.wrapper}>
            {parts.map((part, i) => {
              // block math : $$...$$
              const block = part.match(/^\$\$([\s\S]+)\$\$$/);
              if (block) {
                const math = block[1];
                if (Platform.OS === 'web') {
                  const html = renderToString(math, {
                    throwOnError: false,
                    displayMode: true,
                  });
                  // @ts-ignore
                  return <div key={i} dangerouslySetInnerHTML={{ __html: html }} style={styles.blockWeb}/>
                } else {
                  return (
                    <WebView
                      key={i}
                      originWhitelist={['*']}
                      source={{ html: htmlTemplate(math, true) }}
                      style={[styles.blockMobile, { width }]}
                      scrollEnabled={false}
                    />
                  );
                }
              }

              // inline math : $...$
              const inline = part.match(/^\$([^$]+)\$$/);
              if (inline) {
                const math = inline[1];
                if (Platform.OS === 'web') {
                  const html = renderToString(math, {
                    throwOnError: false,
                    displayMode: false,
                  });
                  // @ts-ignore
                  return <div key={i} dangerouslySetInnerHTML={{ __html: html }} style={styles.inlineWeb}/>
                } else {
                  return (
                    <WebView
                      key={i}
                      originWhitelist={['*']}
                      source={{ html: htmlTemplate(math, false) }}
                      style={styles.inlineMobile}
                      scrollEnabled={false}
                    />
                  );
                }
              }

              // texte brut
              return (
                <Text key={i} style={styles.plainText}>
                  {part}
                </Text>
              );
            })}
          </View>
        );
      })}
    </View>
  );
}

const styles = StyleSheet.create({
  container: {
    flexGrow: 0,
  },
  wrapper: {
    flexDirection: 'row',
    flexWrap: 'wrap',
    alignItems: 'center',    // centre texte & math inline
    marginVertical: 4,        // espace entre paragraphes
  },
  plainText: {
    fontSize: 16,
    lineHeight: 24,
    marginRight: 2,
  },
  inlineWeb: {
    display: 'inline-block',
    whiteSpace: 'normal',
    wordWrap: 'break-word',
    wordBreak: 'break-all',
    marginHorizontal: 2,
    flexShrink: 1,
    minWidth: 0,
  },
  blockWeb: {
    display: 'block',
    width: '100%',
    whiteSpace: 'normal',
    wordWrap: 'break-word',
    wordBreak: 'break-all',
    marginVertical: 8,
    flexShrink: 0,
  },
  inlineMobile: {
    height: 40,
    backgroundColor: 'transparent',
    marginHorizontal: 2,
    flexShrink: 1,
    minWidth: 0,
  },
  blockMobile: {
    height: 150,
    backgroundColor: 'transparent',
    marginVertical: 8,
    flexShrink: 0,
  },
});
